

import { Directive } from '@angular/core';

@Directive({
  selector: 'tds-page-header-title, [tds-page-header-title]',
  exportAs: 'tdsPageHeaderTitle',
  host: {
    class: 'tds-page-header-heading-title text-neutral-1-900 dark:text-d-neutral-1-900 text-header-1 font-semibold pr-3'
  }
})
export class TDSPageHeaderTitleDirective {}

@Directive({
  selector: 'tds-page-header-subtitle, [tds-page-header-subtitle]',
  exportAs: 'tdsPageHeaderSubtitle',
  host: {
    class: 'tds-page-header-heading-sub-title text-caption-1 font-regular text-neutral-1-400 dark:text-d-neutral-1-400 pr-3'
  }
})
export class TDSPageHeaderSubtitleDirective {}

@Directive({
  selector: 'tds-page-header-content, [tds-page-header-content]',
  exportAs: 'tdsPageHeaderContent',
  host: {
    class: 'tds-page-header-content  px-4 py-2.5 w-full block'
  }
})
export class TDSPageHeaderContentDirective {}

@Directive({
  selector: 'tds-page-header-tags, [tds-page-header-tags]',
  exportAs: 'tdsPageHeaderTags',
  host: {
    class: 'tds-page-header-heading-tags'
  }
})
export class TDSPageHeaderTagDirective {}

@Directive({
  selector: 'tds-page-header-extra, [tds-page-header-extra]',
  exportAs: 'tdsPageHeaderExtra',
  host: {
    class: 'tds-page-header-heading-extra flex '
  }
})
export class TDSPageHeaderExtraDirective {}

@Directive({
  selector: 'tds-page-header-footer, [tds-page-header-footer]',
  exportAs: 'tdsPageHeaderFooter',
  host: {
    class: 'tds-page-header-footer  border-t border-neutral-2-200 dark:border-d-neutral-2-200 px-4 py-2.5 w-full block'
  }
})
export class TDSPageHeaderFooterDirective {}

@Directive({
  selector: 'tds-breadcrumb[tds-page-header-breadcrumb]',
  exportAs: 'tdsPageHeaderBreadcrumb',
  host:{
    class:'px-4 pt-2.5 pb-1'
  }
})
export class TDSPageHeaderBreadcrumbDirective {}

@Directive({
  selector: 'tds-avatar[tds-page-header-avatar]',
  exportAs: 'tdsPageHeaderAvatar'
})
export class TDSPageHeaderAvatarDirective {}
